/* Global Styles */
* {
  box-sizing: border-box;
}
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: var(--background-color);
  color: var(--text-color);
}

.App {
  text-align: center;
}

/* Light Theme Variables */
:root {
  --background-color: #f9f9f9;
  --text-color: #000;
  --not-text: #000;
  --task-background-color: #a2dff2;
  --completed-task-background-color: #e0ffe0;
  --not-completed-color: #efbbbb;
  --border-color: #ddd;
  --input-background-color: #fff;
  --button-background-color: #306100;
  --button-text-color: #fff;
  --delete-btn-color: #ff4d4d;
  --stroke: -1px -1px 0 #000000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  --scrollbar-color: #fff5f5;
  --scrollbar-thumb-color: #888;
}

/* Dark Theme Variables */
[data-theme="dark"] {
  --background-color: #545151;
  --text-color: #f9f9f9;
  --task-background-color: #2896;
  --completed-task-background-color: #4caf50;
  --border-color: #555;
  --not-text: #fff;
  --not-completed-color: #eb7c7c;
  --input-background-color: #444;
  --button-background-color: #4caf50;
  --button-text-color: #fff;
  --delete-btn-color: #ff6b6b;
  --stroke: -1px -1px 0 #FFF, 1px -1px 0 #FFF, -1px 1px 0 #FFF, 1px 1px 0 #FFF;
  --scrollbar-color: #939292;
  --scrollbar-thumb-color: #666;
}

/* To-Do List Container */
#todo-list {
  width: 800px;
  margin: 20px 10px;
  background-color: var(--task-background-color);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  min-height: 85vh;
}

@media (max-width: 728px) {
  #todo-list {
    width: 85vw;
  }
}

/* Completed Task */
.task.completed {
  background-color: var(--completed-task-background-color);
}

/* Heading */
#todo-list h2 {
  color: var(--text-color);
  border: none;
}

/* Task */
.task {
  display: flex;
  justify-content: space-between;
  text-align: left;
  align-items: center;
  padding: 12px;
  border: 1px solid var(--border-color);
  border-radius: 6px;
  margin-bottom: 12px;
  background-color: var(--not-completed-color);
  color: var(--not-text);
}

/*modify*/
.modify {
  display: flex;
}

/* Checkbox */
.task input[type="checkbox"] {
  padding: 5px;
  width: 15px;
  height: 15px;
  margin-right: 12px;
  color: green;
}

.strikethrough {
  text-decoration: line-through;
}

/* Delete Button */
.delete-btn {
  text-shadow: var(--stroke);
  cursor: pointer;
  color: var(--delete-btn-color);
  font-weight: bold;
  display: block;
  margin-left: 8px;
}

/* Input */
#task-input {
  flex: 1;
  padding: 8px;
  margin-right: 8px;
  background-color: var(--input-background-color);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-color);
  margin-top: 10px;
}

/* Add Button */
#add-btn {
  padding: 10px 12px;
  background-color: var(--button-background-color);
  color: var(--button-text-color);
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

/*Theme container*/
.theme-container {
  width: 100%;
  height: 50px;
  display: block;
}
/* Add Task Container */
#add-task-container {
  margin-bottom: 25px;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

/* Tasks Container */
#tasks-container {
  max-height: 60vh;
  padding-right: 10px;
  overflow-y: auto;
  margin-bottom: 12px;
  -webkit-user-select: none;
  user-select: none;
}

/* Custom Scrollbar */
#tasks-container::-webkit-scrollbar {
  width: 10px;
}

#tasks-container::-webkit-scrollbar-track {
  background: var(--scrollbar-color);
}

#tasks-container::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb-color);
  border-radius: 6px;
  border: 3px solid var(--scrollbar-color);
}

/* Empty Note */
#empty-note {
  text-align: center;
  font-weight: bold;
  margin: 100px auto;
}

/* Theme Toggle Button */
#theme-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 10px 12px;
  background-color: var(--button-background-color);
  color: var(--button-text-color);
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
